.navbar {
    width: 100vw;
    height: 50px;
    background-color: rgba(3, 3, 3, 0.5);

    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    gap: 20px;

    /* rem 반응형 기본 - 실무에서 거의 이거 씀 */
    padding: 1rem; /* 2개 쓴다고 하면 위아래가 먼저 적용  */
    z-index: 1000;
    font-size: 1.5rem;
}

.navbar a {
    text-decoration: none;
    color: white;
    font-size: 1.5rem;
    font-weight: bold;
    transition: all 0.3s ease;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
}

.navbar a:hover {
    color: #ff6347;
}