.scrollText {
    width: 100vw;
    height: 300vh;
}

.scrollt{
    position: fixed;
    top: 40%;
    text-align: center;
    transition: all 1s ease;
    opacity: 0;
}

.scrollt.on {
    opacity: 1;
}

.scrolli {
    width: 100%;
    position: fixed;
    transition: all 1s ease;
    opacity: 0;
}

.scrolli.on {
    opacity: 1;

}

.scrolliImg {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    transform: translateY(100%);
    transition: all 1s ease;
}

.scrolli.on .scrolliImg {
    transform: translateY(0);
}