.galleryOutbox {
    width: 100vw;
    overflow-x: hidden;
    /* y스크롤은 사용할 거기 때문에 */
}

/* 반복되는 부분의 전체 박스 */
.scrollImg {
    overflow: hidden;
    padding: 40px;
}
/* 반복되는 이미지 나오는 트랙 - 가로로 펼쳐져야 함 */
.scrollTrack {
    display: flex;
    gap: 30px;

    width: max-content; /* 안에 있는 컨텐츠만큼 커져라 */

    animation: leftscroll 40s linear infinite;
    animation-play-state: running; /* 호버할 때 퓨즈를 주면 잠깐 멈추게 할 수 있음 */
}

.scrollImg:hover .scrollTrack {
    animation-play-state: paused;
}

.scrollInbox {
    width: 300px;
    /* height: 300px; */
    text-align: center;
    transition: all 1s ease;
}

.scrollInbox:hover {
    transform: scale(1.05);
}

.scrollInbox img {
    width: 100%;
    height: 300px;
    border-radius: 5px;
    /* 이미지는 반드시 넣어줘야 함 */
    object-fit: cover;
    /* 이미지 */
}

@keyframes leftscroll {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-30%);
    }
}

/* 페이드 이미지 시작 */
.fadeSection {
    display: flex;
    /* gap: 40px; */
    align-items: center;
    max-width: 1200px;
}

.imageBox {
    width: 50%;
    margin: 20px auto;

}

.imageBox img {
    width: 600px;
    height: 400px;
    object-fit: cover;
    margin: 50px auto;
    border-radius: 3px;
}

/* 모아지는 이미지 */
.groupImage {
    display: flex;
    width: 70%;
    justify-content: center;
    gap: 30px;
    align-items: center;
}

.groupImage img {
    width: 300px;
    height: 400px;
    border-radius: 5px;

    object-fit: cover; /* 이미지 자체를 다룰 때 */
}